type AttachmentAndClassification = Office.AttachmentDetailsCompose & { classification: ClassificationType };
const attachmentsStyles = (attachment: AttachmentAndClassification) => {
  let arr: { color: string; name: string } = null;
  switch (attachment.classification.classificationLabel) {
    case "public":
      arr = {
        color: "#107c10",
        name: "Público",
      };
      break;
    case "restricted":
      arr = {
        color: "#d83b01",
        name: "Restringido",
      };
      break;
    case "confidential":
      arr = {
        color: "#e81123",
        name: "Confidencial",
      };
      break;
    case "internalUse":
      arr = {
        color: "#d29200",
        name: "Uso interno",
      };
      break;
    default:
      null;
  }
  return arr;
};
